
.me{
  background: white;
  font-family: "Open Sans", sans-serif;
  color: #525f7f;
  font-size: 14px;
  padding-bottom: 300px;

}
.heading_2{
  font-size: 30px;
  padding :20px;
  text-align: center;
  background-color: white;
}
.timelines {
  display: flex;
  flex-direction: column;
  margin: 20px auto;
  position: relative;
}
.timelines__event {
  margin-bottom: 20px;
  position: relative;
  display: flex;
  margin: 20px 0;
  border-radius: 6px;
  align-self: center;
  width: 50vw;
}
.timelines__event:nth-child(2n + 1) {
  flex-direction: row-reverse;
}
.timelines__event:nth-child(2n + 1) .timelines__event__date {
  border-radius: 0 6px 6px 0;
}
.timelines__event:nth-child(2n + 1) .timelines__event__content {
  border-radius: 6px 0 0 6px;
}
.timelines__event:nth-child(2n + 1) .timelines__event__icon:before {
  content: "";
  width: 2px;
  height: 100%;
  background: #f6a4ec;
  position: absolute;
  top: 0%;
  left: 50%;
  right: auto;
  z-index: -1;
  transform: translateX(-50%);
}
.timelines__event:nth-child(2n + 1) .timelines__event__icon:after {
  content: "";
  width: 100%;
  height: 2px;
  background: #f6a4ec;
  position: absolute;
  right: 0;
  z-index: -1;
  top: 50%;
  left: auto;
  transform: translateY(-50%);
}
.timelines__event__title {
  font-size: 1.2rem;
  line-height: 1.4;
  text-transform: uppercase;
  font-weight: 600;
  color: #9251ac;
  letter-spacing: 1.5px;
}
.timelines__event__content {
  padding: 20px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  background: #fff;
  width: calc(40vw - 84px);
  border-radius: 0 6px 6px 0;
}
.timelines__event__date {
  color: #f6a4ec;
  font-size: 1.5rem;
  font-weight: 600;
  background: #9251ac;
  display: flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  padding: 0 20px;
  border-radius: 6px 0 0 6px;
}
.timelines__event__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #9251ac;
  padding: 20px;
  align-self: center;
  margin: 0 20px;
  background: #f6a4ec;
  border-radius: 100%;
  width: 40px;
  box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.025);
  padding: 40px;
  height: 40px;
  position: relative;
}
.timelines__event__icon i {
  font-size: 20px;
  font-weight: bold;
}
.timelines__event__icon:before {
  content: "";
  width: 2px;
  height: 100%;
  background: #f6a4ec;
  position: absolute;
  top: 0%;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
}
.timelines__event__icon:after {
  content: "";
  width: 100%;
  height: 2px;
  background: #f6a4ec;
  position: absolute;
  left: 0%;
  z-index: -1;
  top: 50%;
  transform: translateY(-50%);
}
.timelines__event__description {
  flex-basis: 60%;
}
.timelines__event--type2:after {
  background: #555ac0;
}
.timelines__event--type2 .timelines__event__date {
  color: #87bbfe;
  background: #555ac0;
}
.timelines__event--type2:nth-child(2n + 1) .timelines__event__icon:before, .timelines__event--type2:nth-child(2n + 1) .timelines__event__icon:after {
  background: #87bbfe;
}
.timelines__event--type2 .timelines__event__icon {
  background: #87bbfe;
  color: #555ac0;
}
.timelines__event--type2 .timelines__event__icon:before, .timelines__event--type2 .timelines__event__icon:after {
  background: #87bbfe;
}
.timelines__event--type2 .timelines__event__title {
  color: #555ac0;
}
.timelines__event--type3:after {
  background: #24b47e;
}
.timelines__event--type3 .timelines__event__date {
  color: #aff1b6;
  background-color: #24b47e;
}
.timelines__event--type3:nth-child(2n + 1) .timelines__event__icon:before, .timelines__event--type3:nth-child(2n + 1) .timelines__event__icon:after {
  background: #aff1b6;
}
.timelines__event--type3 .timelines__event__icon {
  background: #aff1b6;
  color: #24b47e;
}
.timelines__event--type3 .timelines__event__icon:before, .timelines__event--type3 .timelines__event__icon:after {
  background: #aff1b6;
}
.timelines__event--type3 .timelines__event__title {
  color: #24b47e;
}
.timelines__event:last-child .timelines__event__icon:before {
  content: none;
}